.reset-btn {
  position: absolute;
  bottom: 0;
  right: 0;
  padding: 7px;
  font-size: 17px;
  text-transform: lowercase;
  border-radius: 5px;
  color: var(--saffron);
}
.reset-btn--hidden {
  opacity: 0;
  visibility: hidden;
}

.arrow-link {
  margin-left: 15px;
  position: relative;
  column-gap: 10px;
}
.arrow-link--new {
  padding: 2px 10px;
  margin-left: 15px;
  width: fit-content;
  height: 100%;
  background-color: var(--white);
  color: var(--papa-green);
  font-size: 12px;
  font-weight: 700;
  line-height: 133.33%;
  text-transform: lowercase;
  border-radius: 4px;
  transition: all 0.2s;
}
.arrow-link:hover .arrow-link--new {
  transform: scale(1.5);
  filter: blur(1);
  color: var(--saffron);
  background-color: var(--papa-green);
}
.arrow-link::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: white;
  transition: width 0.2s;
}
.arrow-link svg {
  position: absolute;
  top: 50%;
  left: -15px;
  transform: translateY(-50%);
  width: 6px;
  height: 10px;
  fill: var(--white);
  transition: left 0.2s;
}
.arrow-link:hover {
  color: var(--saffron);
}
.arrow-link:hover::after {
  width: 100%;
  background-color: var(--saffron);
}
.arrow-link:hover svg {
  left: -8px;
  fill: var(--saffron);
}

