.nav__link::after {
  content: "";
  position: absolute;
  bottom: -7%;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #fff;
  transition: width ease-in 0.2s;
}
.nav__link::before {
  content: "";
  position: absolute;
  bottom: -7%;
  right: 0;
  width: 0;
  height: 2px;
  background-color: #fff;
  transition: width ease-in 0.2s;
