.page-404 {
  height: 100vh;
  background-color: var(--papa-green);
  color: var(--white);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.page-404 .text {
  display: block;
  margin-bottom: 70px;
  font-size: 100px;
}
.page-404 span {
  display: block;
  margin-bottom: 30px;
  font-size: 20px;
}
.page-404 .glow {
  padding: 20px;
  position: relative;
  display: block;
  font-weight: 900;
  border-radius: 15px;
  color: var(--cinder);
  background-color: var(--white);
  z-index: 0;
}
.page-404 .glow::after {
  content: "";
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  left: 0;
  top: 0;
  border-radius: 10px;
}
.page-404 .glow::before {
  content: "";
  background: linear-gradient(45deg, #FF0000, #FF7300, #FFFB00, #48FF00, #00FFD5, #002BFF, #FF00C8, #FF0000);
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 600%;
  z-index: -1;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  filter: blur(8px);
  animation: glowing 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}
@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
.page-404 .content .text {
  position: relative;
  color: #fff;
  font-weight: 700;
  transform: scale(2);
  padding: 30px;
  letter-spacing: 2px;
  text-transform: uppercase;
}
.page-404 .content .text:before,
.page-404 .content .text:after {
  padding: 30px;
  color: white;
  content: attr(data-text);
  position: absolute;
  width: 100%;
  height: 100%;
  background: var(--papa-green);
  overflow: hidden;
  top: 0;
}
.page-404 .content .text:before {
  left: 3px;
  text-shadow: -2px 0 red;
  animation: glitch-1 2s linear infinite reverse;
}
.page-404 .content .text:after {
  left: -3px;
  text-shadow: -2px 0 blue;
  animation: glitch-2 2s linear infinite reverse;
}
